@import 'assets/styles/index.scss';

.back {
  position: relative;
  width: 100%;
  height: 100vh;

  @include mq(tab) {
    background-color: #E7EAF2;
    background-image: url('../../image/Ellipse\ 1.png'), url('../../image/Ellipse\ 2.png');
    background-repeat: no-repeat;
    background-position: left bottom, right top;
  }

  @include mq(desk) {
    height: 100vh;
  }
}

.blur {
  @include mq(desk) {
    position: absolute;
    max-width: 731px;
    top: 0;
    right: 0;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(25px);
  }
}

.registerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;

  @include mq(tab) {
    flex-direction: column;
  }

  @include mq(desk) {
    height: 100vh;
    flex-direction: row;
    justify-content: baseline;
  }
}

.registerImgWrapper {
  display: flex;
  gap: 28px;
  margin-top: 60px;
  margin-bottom: 50px;

  @include mq(desk) {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 0;
    margin-right: 30.9px;
    margin-left: 76px;
  }
}

.appTitle {
  margin-top: 103px;
  font-size: 30px;
  font-family: Poppins;
  font-weight: 400;
  line-height: 1.5;

  @include mq(desk) {
    margin-top: 0;
  }
}

.form {
  @include mq(tab) {
    padding-bottom: 196px;
  }

  @include mq(desk) {
    display: flex;
    align-items: center;
    height: 100vh;
    margin-right: -16px;
    padding-left: 107px;
    padding-top: 136px;
    padding-right: 91px;
    padding-bottom: 116px;
    background-color: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(25px);
  }
}